    <!--    <el-divider></el-divider>-->
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item prop="ProductName">
            <el-input v-model="ruleForm.ProductName">
              <template slot="prepend">商品名</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item prop="CategoryI">
            <el-input v-model="ruleForm.CategoryI">
              <template slot="prepend">カテゴリー</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <!--      <el-row :gutter="20">-->
      <!--        <el-col :span="8">-->
      <!--          <el-form-item prop="CategoryII">-->
      <!--            <el-input v-model="ruleForm.CategoryII">-->
      <!--              <template slot="prepend">大カテゴリ</template>-->
      <!--            </el-input>-->
      <!--          </el-form-item>-->
      <!--        </el-col>-->
      <!--        <el-col :span="8">-->
      <!--          <el-form-item prop="CategoryIII">-->
      <!--            <el-input v-model="ruleForm.CategoryIII">-->
      <!--              <template slot="prepend">小カテゴリ</template>-->
      <!--            </el-input>-->
      <!--          </el-form-item>-->
      <!--        </el-col>-->
      <!--      </el-row>-->
      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item prop="Price">
            <el-input v-model="ruleForm.Price">
              <template slot="prepend">価格</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item>
            <el-button-group>
              <el-button type="warning" @click="cancelForm()">キャンセル</el-button>
              <el-button type="primary" icon="el-icon-finished" @click="submitForm('ruleForm')">登録</el-button>
            </el-button-group>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
import {postProductData, getProductDataOne} from 'api/products'
// import PageHeader from 'components/common/menu/PageHeader'


export default {
  name: 'ProductForm',
  components: {
    // PageHeader
  },
  props: {
    productuuid: {
      type: String, //指定传入的类型
      //type 也可以是一个自定义构造器函数，使用 instanceof 检测。
      default: '' //这样可以指定默认的值
    }
  },
  data() {
    return {
      ruleForm: {
        ProductName: '',
        CategoryI: '',
        // CategoryII: '',
        // CategoryIII: '',
        Price: ''
      },
      rules: {
        ProductName: [
          {required: true, message: '商品名を入力してください', trigger: 'blur'}
        ],
        Price: [
          {required: true, message: '金額を入力してください', trigger: 'blur'}
        ]
      }
    }
  },
  created: function () {
    if (this.productuuid) {
      this.getProductDataOne()
    }
  },
  methods: {
    postProductData() {
      let values = {}
      values['uuid'] = this.productuuid
      values['ProductName'] = this.ruleForm.ProductName
      values['CategoryI'] = this.ruleForm.CategoryI
      values['CategoryII'] = this.ruleForm.CategoryII
      values['CategoryIII'] = this.ruleForm.CategoryIII
      values['Price'] = this.ruleForm.Price
      postProductData(values).then(res => {
        if (res.status == 200) {
          this.$emit('submit')
          this.$message({
            showClose: true,
            message: '商品を登録・更新成功しました。',
            type: 'success'
          });
        } else {
          this.$emit('cancel')
          this.$message({
            showClose: true,
            message: '商品を登録・更新失敗しました。',
            type: 'error'
          });
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getProductDataOne() {
      let values = {}
      values['uuid'] = this.productuuid
      getProductDataOne(values).then(res => {
        this.ruleForm.ProductName = res.data.data[0].ProductName
        this.ruleForm.CategoryI = res.data.data[0].CategoryI
        this.ruleForm.Price = res.data.data[0].Price
      }).catch(err => {
        console.log(err)
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.postProductData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    cancelForm() {
      this.$emit('cancel')
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

