import {request} from 'network/request'

export function postProductData(parameter) {
  return request({
    url: '/PostProductData',
    method: 'post',
    data: parameter
  })
}

export function getProductDataAll(parameter) {
  return request({
    url: '/GetProductDataAll',
    method: 'get',
    data: parameter
  })
}

export function getProductDataOne(parameter) {
  return request({
    url: '/GetProductDataOne',
    method: 'post',
    data: parameter
  })
}

export function searchProductData(parameter) {
  return request({
    url: '/SearchProductData',
    method: 'post',
    data: parameter
  })
}
